import Sprite from '../enums/Sprite.js';

export default class Backdrop {
    constructor(x = 400, y = 300) {
        if (new.target === Backdrop) {
            throw new TypeError("Cannot construct abstract instances directly.");
        }

        this.direction = 0;
        this.x = x;
        this.y = y;
        this.sprite = Sprite.BOX;
    }

    update() {

    }
}